<script>
  import { ActivityStore } from "../stores/Activity.store";

  function UpdateSettings() {
    localStorage.setItem(
      "__ACTIVITY_WORKING_TIME__",
      ActivityStore.workingTime
    );

    localStorage.setItem(
      "__ACTIVITY_RESTING_TIME__",
      ActivityStore.restingTime
    );

    localStorage.setItem(
      "__ACTIVITY_AUTOMATIC_SWAP__",
      ActivityStore.automatic
    );
  }
</script>

<div class="Page Settings">
  <h2 class="App--Title">Settings</h2>

  <div class="Settings--Form">
    <div class="Settings--Field">
      <label for="Settings__WorkingTime">Working Time</label>
      <input
        id="Settings__WorkingTime"
        type="number"
        placeholder="Working Time"
        name="Settings__WorkingTime"
        bind:value={ActivityStore.workingTime}
      />
    </div>

    <div class="Settings--Field">
      <label for="Settings__RestingTime">Resting Time</label>
      <input
        id="Settings__RestingTime"
        type="number"
        placeholder="Resting Time"
        name="Settings__RestingTime"
        bind:value={ActivityStore.restingTime}
      />
    </div>

    <div class="Settings--Field">
      <label for="Settings__Automatic">Automatic</label>
      <select
        id="Settings__Automatic"
        placeholder="Automatic"
        name="Settings__Automatic"
        bind:value={ActivityStore.automatic}
      >
        <option value="true">Yes</option>
        <option value="false">No</option>
      </select>
    </div>

    <div class="Settings--Field">
      <button on:click={UpdateSettings}>Save</button>
    </div>
  </div>
</div>

