<script>
  import { ActivityStore } from "../../stores/Activity.store.js";

  let status;

  const setWorkingStatus = () => ActivityStore.toggleTimer("working");
  const setRestingStatus = () => ActivityStore.toggleTimer("resting");

  ActivityStore.activityStatus.subscribe(data => (status = data));
</script>

<style>
  footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 60px;
    text-align: center;
    font-size: var(--xsFontSize);
    color: var(--secondaryInverseColor);
    text-transform: uppercase;
    display: flex;
    flex-direction: row;
  }

  footer .Footer--Button {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    cursor: pointer;
    font-family: inherit;
    font-size: var(--smFontSize);
    text-transform: uppercase;
    font-weight: 600;
    color: var(--primaryInverseColor);
    opacity: 0.85;
    transition: opacity 200ms linear;
    outline: none;
  }

  footer .Footer--Button:hover {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2);
    opacity: 1;
  }

  footer .Footer--Button.rest {
    background-color: var(--primaryColor);
  }

  footer .Footer--Button.work {
    background-color: var(--secondaryColor);
  }

  footer .Footer--Button.work.active,
  footer .Footer--Button.rest.active {
    background-color: var(--githubLogoColor);
  }
</style>

<footer>
  <button
    class={status == 'resting' ? 'Footer--Button rest active' : 'Footer--Button rest'}
    on:click={setRestingStatus}>
    {#if status != 'resting'}Start Rest{:else}Pause Rest{/if}
  </button>

  <button
    class={status == 'working' ? 'Footer--Button work active' : 'Footer--Button work'}
    on:click={setWorkingStatus}>
    {#if status != 'working'}Start Work{:else}Pause Work{/if}
  </button>
</footer>
